/*============================================================
    .navigation.sticky.scrolling
============================================================*/


.navigation.sticky {
    position: fixed;
    background-color: #fff;
    visibility: hidden;
    opacity: 0;
    width: 100%;
    height: 60px;
    top: 0;
    z-index: 120;
}

.navigation.sticky.scrolling {
    opacity: 1;
    visibility: visible;
}

.navigation.sticky #nav-icon span {
    background-color: #444444;
}

.navigation.offset {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.navigation.scrolling {
    -webkit-transform: translate3d(0, 0px, 0);
    transform: translate3d(0, 0px, 0);
}

.navigation.sticky.scrolling {
    opacity: 1;
    visibility: visible;
    box-shadow: 0 0 15px -4px #999;
    padding: 3px 0;
    background: #0c0c0c;
}



.navigation.sticky.scrolling .top-logo {
    max-width: 110px;
    display: none;
}


/*============================================================
    #cssmenu
============================================================*/

#cssmenu,
#cssmenu ul,
#cssmenu ul li,
#cssmenu ul li a,
#cssmenu #head-mobile {
    border: 0;
    list-style: none;
    line-height: 1;
    display: block;
    position: relative;
    box-sizing: border-box
}

nav#cssmenu {
    text-align: center;
    line-height: 0;
    margin-top: 25px;
    float: left;
    margin-left: 25px;
}

.navigation.sticky.scrolling nav#cssmenu {
    text-align: center;
    margin-top: 10px;
    float: none;
}

.navigation.sticky.scrolling .open_status {
    display: none;
}

#cssmenu:after,
#cssmenu > ul:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0
}

#cssmenu #head-mobile {
    display: none
}

#cssmenu ul {
    float: none;
    text-align: center;
    display: inline-block;
}

#cssmenu > ul > li {
    display: inline-block;
    padding-left: 30px;
}

#cssmenu > ul > li > a {
    padding: 10px;
    font-size: 14px;
    letter-spacing: 0.7px;
    text-decoration: none;
    color: #333;
    font-weight: 500;
    text-transform: uppercase;
    border-top: 2px solid transparent;
}

.navigation.sticky.scrolling #cssmenu > ul > li > a {
    color: #fff;
}

.navigation.sticky.scrolling #cssmenu > ul > li > a:hover {
    color: #3B8BeB;
}

#cssmenu > ul > li:hover > a,
#cssmenu ul li.active a {
    color: #7030a0;
    border-top: 2px solid #7030a0;
    background: transparent;
}

ul.sub-menu li a {
    border-top: none !important;
}

#cssmenu > ul > li:hover,
#cssmenu ul li.active:hover,

#cssmenu ul li.has-sub.active:hover {
    /*   background:#1a3769!important;*/
    -webkit-transition: background .3s ease;
    transition: background .3s ease;
}

#cssmenu > ul > li.has-sub > a {
    padding-right: 10px !important;
}

#cssmenu > ul > li.has-sub > a:after {
    position: absolute;
    top: 40px;
    right: 11px;
    width: 8px;
    height: 2px;
    /* display: block; */
    /* background: #fff; */
    content: '';
}

#cssmenu > ul > li.has-sub > a:after {
    /* top: 29px; */
    /* background: #777; */
}

#cssmenu > ul > li.has-sub > a:before {
    position: absolute;
    right: -5px;
    display: block;
    font-family: FontAwesome;
    content: "\f107";
    -webkit-transition: all .25s ease;
    transition: all .25s ease;
}

#cssmenu > ul > li.has-sub > a:before {
    color: #7030a0;
}

#cssmenu > ul > li.has-sub:hover > a:before {
    /* top: 23px; */
    /* height: 0; */
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

#cssmenu ul ul {
    position: absolute;
    left: -9999px;
    z-index: 111;
    background: #fff;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transition: -webkit-transform 0.2s linear;
    transition: -webkit-transform 0.2s linear;
    transition: transform 0.2s linear;
    transition: transform 0.2s linear, -webkit-transform 0.2s linear;
    border-radius: 4px;
    border: 2px solid #7030a0;
}

#cssmenu li:hover > ul {
    left: 0;
}

#cssmenu ul ul li {
    height: 0;
    -webkit-transition: all .25s ease;
    -ms-transition: all .25s ease;
    background: #e5dfec;
    transition: all .25s ease
}

#cssmenu ul ul li:hover {}

#cssmenu li:hover > ul {
    left: 0;
}

#cssmenu li:hover > ul > li {
    height: 100%;
}

#cssmenu ul ul ul {
    margin-left: 100%;
    top: 0
}

#cssmenu ul ul li a {
    border-bottom: 2px solid rgb(112, 48, 160);
    padding: 11px 15px;
    width: 190px;
    font-size: 13px;
    text-decoration: none;
    color: #333;
    font-weight: 500;
    line-height: 13px;
}

#cssmenu ul ul li:last-child > a,
#cssmenu ul ul li.last-item > a {
    border-bottom: 0
}

#cssmenu ul ul li:hover > a,
#cssmenu ul ul li a:hover {
    color: #fff;
    background: #7030a0;
}

#cssmenu ul ul li.has-sub > a:after {
    position: absolute;
    top: 16px;
    right: 11px;
    width: 8px;
    height: 2px;
    display: block;
    background: #ddd;
    content: ''
}

#cssmenu ul ul li.has-sub > a:before {
    position: absolute;
    top: 13px;
    right: 14px;
    display: block;
    width: 2px;
    height: 8px;
    background: #ddd;
    content: '';
    -webkit-transition: all .25s ease;
    transition: all .25s ease
}

#cssmenu ul ul > li.has-sub:hover > a:before {
    top: 17px;
    height: 0
}

#cssmenu ul ul li.has-sub:hover,
#cssmenu ul li.has-sub ul li.has-sub ul li:hover {
    background: #555;
}

#cssmenu ul ul ul li.active a {
    border-left: 1px solid #333
}

#cssmenu > ul > li.has-sub > ul > li.active > a,
#cssmenu > ul ul > li.has-sub > ul > li.active > a {
    border-top: 1px solid #333
}

