.middle-video__overlay {
    position: fixed;
    z-index: 999;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
    opacity: 0;
}

.middle-video__overlay--active {
    opacity: 1;
}

.middle-video__modal {
    width: 90%;
    margin: 30px auto 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.middle-video__close {
    position: absolute;
    z-index: 15;
    right: 0;
    bottom: 100%;
    width: 50px;
    height: 50px;
    text-indent: -9999em;
}

.middle-video__close:before,
.middle-video__close:after {
    content: '';
    position: absolute;
    width: 30px;
    top: 50%;
    left: 50%;
    height: 2px;
    margin-left: -15px;
    background: #fff;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.middle-video__close:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

